.sessionlist{
    position: absolute;
    left: 80px;
    top: 0;
    bottom: 0;
    right: 0;
    width: 240px;
    border-right:1px solid #eee;
}
    .sessionlist .session-item-outer.selected{
    background:#abcde4;
}
    .sessionlist .session-item-outer .session-item{
            padding:10px 10px;
            display:-webkit-box;
            display:-ms-flexbox;
            display:flex;
            text-decoration: none;

            color:#333;
}
    .sessionlist .session-item-outer .session-item .ctn-avator .iconfont{
                    font-size:36px;
                }
    .sessionlist .session-item-outer .session-item .session-inner{
                margin-left:10px;
            }



