/**
 *  做一些通用样式
 *  比如设置button的样式
**/
.button{
    font-size: 16px;
    box-sizing: border-box;
    display: inline-block;
    margin: 10px auto;
    width: 300px;
    height: 36px;
    cursor: pointer;
    border-radius: 2px;
    border: 1px solid #4eb1f4;
    background-color:#4eb1f4;
    color:#FFF;
}
.input{
    box-sizing: border-box;
    font-size: 14px;
    padding: 0 4px;
    display: inline-block;
    margin: 10px auto 0;
    width: 300px;
    height: 36px;
    line-height: 36px;
    cursor: pointer;
    border-radius: 2px;
    border: 1px solid #e5e5e5;
    -webkit-tap-highlight-color: transparent;
    background-color: white;
    outline: none;
}
.sign{
    background: #FFF;
    width: 400px;
    margin: auto;
    margin-top: 100px;
    padding: 30px;
    border-radius: 3px;
}
.loading {
    position: fixed;
    left: 0;
    top: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, .5);
    text-align: center;
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}


